@layer components {
    .formulario {
        
        *, *:before, *:after {
        box-sizing: border-box;
        }

        a {
            text-decoration: none;
            color: #1ab188;
            transition: 0.5s ease;
        }
        
        a:hover {
            color: #179b77;
        }
        
        .contenedor-formularios {
            background: rgba(19, 35, 47, 0.9);
            padding: 40px;
            max-width: 600px;
            margin: 40px auto;
            border-radius: 4px;
            box-shadow: 0 4px 10px 4px rgba(19, 35, 47, 0.3);
            margin-top: 20vh;
        }
        
        .contenedor-tabs {
            list-style: none;
            padding: 0;
            margin: 0 0 40px 0;
        }

        .contenedor-tabs:after {
            content: "";
            display: table;
            clear: both;
        }

        .contenedor-tabs li a {
            display: block;
            text-decoration: none;
            padding: 15px;
            background: rgba(160, 179, 176, 0.25);
            color: #a0b3b0;
            font-size: 20px;
            float: left;
            width: 50%;
            text-align: center;
            cursor: pointer;
            transition: 0.5s ease;
        }

        .contenedor-tabs li a:hover {
            background: #179b77;
            color: #fff;
        }

        .contenedor-tabs .active a {
            background: #1ab188;
            color: #fff;
        }
        
        h1 {
            text-align: center;
            color: #fff;
            font-weight: 300;
            font-size: 24px;
            margin: 0 0 40px;
        }
        
        label {
            color: rgba(255, 255, 255, 0.5);
            font-size: 22px;
        }

        label .req {
            margin: 2px;
            color: #1ab188;
        }
        
        label.active {
            transform: translateY(50px);
            left: 2px;
            font-size: 14px;
        }

        label.active .req {
            opacity: 0;
        }
        
        label.highlight {
            color: #fff;
        }
        
        input {
            font-size: 22px;
            display: block;
            width: 100%;
            height: 40px;
            padding: 5px 10px;
            background: none;
            background-image: none;
            border: 1px solid #a0b3b0;
            color: #fff;
            border-radius: 0;
            transition: all 0.5s ease;
            border-radius: 5px;
        }

        input:focus {
            outline: none;
            border-color: #1ab188;
        }
        
        .contenedor-input {
            position: relative;
            margin-bottom: 40px;
        }
        
        .fila-arriba:after {
            content: "";
            display: table;
            clear: both;
        }

        .fila-arriba div {
            float: left;
            width: 48%;
            margin-right: 4%;
        }

        .fila-arriba div:last-child {
            margin: 0;
        }
        
        .button {
            height: 65px;
            border: 0;
            outline: none;
            border-radius: 5px;
            cursor: pointer;
            padding: 15px 0;
            font-size: 2rem;
            background: #1ab188;
            color: #fff;
            transition: all 0.5s ease;
            appearance: none;
            -webkit-appearance: none;
        }
        
        .button:hover, .button:focus {
            background: #179b77;
        }
        
        .button-block {
            display: block;
            width: 100%;
        }
        
        .forgot {
            margin-top: -20px;
            text-align: right;
            margin-bottom: 20px;
        }


    }

    @media screen and (max-width: 500px) {
        .fila-arriba div {
            width: 100%;
        }

        .fila-arriba div:last-child {
            margin-bottom: 40px;
        }
    }

    @media screen and (max-width: 400px) {
        .contenedor-tabs li a {
            width: 100%;
        }
    }
}